.card {
    border: 1px solid #F3F3F3;
    padding: 30px;
    width: 210px;
    border-radius: 40px;
    margin-right: 30px;
    margin-bottom: 30px;
    transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;

    .plus {
      cursor: pointer;
    }
  
    &:hover {
      box-shadow: 0px 20px 35px rgba(0, 0, 0, 0.06);
      transform: translateY(-5px);
    }
  
    .favorite {
      position: absolute;
      cursor: pointer;
    }
  
    span {
      font-size:px;
      opacity: 0.5;
      text-transform: uppercase;
    }
  
    b {
      font-size: 13px;
    }
  
    h5 {
      font-weight: 400;
      font-size: 14px;
    }
  }

  @media (max-width: 768px) {
    .card {
      width: 185px;
      margin-right: 15px;
    }
  }

  @media (max-width: 480px) {
    .card {
      width: 210px;
    }
  }